.barchart {
	max-width: 400px;
	margin: 60px 20px 0;
	height: 300px;

	display: -ms-flexbox;

	display: flex;
	-ms-flex-wrap: wrap;
	    flex-wrap: wrap;
	-ms-flex-align: end;
	    align-items: flex-end;
	-ms-flex-pack: distribute;
	    justify-content: space-around;
}

.barchart__title {
	width: 100%;
	margin: 30px 0;
	font-size: 1.3em;
}

.barchart__bar {
	min-height: 65px;
	width: 45%;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	padding: 10px 5px;
	-webkit-transition: height 0.5s;
	-o-transition: height 0.5s;
	transition: height 0.5s;
	border-radius: 10px 10px 0 0;

	background: #3f51b5;
	background: -webkit-linear-gradient(top, #3f51b5 0%,#2e44b2 100%);
	background: -webkit-gradient(linear, left top, left bottom, from(#3f51b5),to(#2e44b2));
	background: -o-linear-gradient(top, #3f51b5 0%,#2e44b2 100%);
	background: linear-gradient(to bottom, #3f51b5 0%,#2e44b2 100%);
	/* filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3f51b5', endColorstr='#2e44b2',GradientType=0 ); */
	color: white;

	font-size: 1.25em;
    line-height: 1.4;
}

@media screen and (min-width: 768px) {
	.barchart { margin: 60px auto 0}
}



